<template>
  <div>
        <vue-particles color="#fff" style="background:#24968b;height:100vh;position:fixed;width:100vw;top:0;"></vue-particles>
      <div class="row justify-content-center mt-5">
        <div class="col-md-6 ">
          <div class="bg-white p-4 shadow rounded-lg" v-if="lokasi.length>0">
            <p class="font-semibold text-center p-4" v-if="$store.state.users">
              Welcome, {{$store.state.users.displayName}}
            </p>
            <p  class="font-semibold text-center p-4" v-else>
              Tidak ada Akun yang terkait!
            </p>
            <hr class="mt-3 mb-3">
            <!-- <p class="p-2">
              Pilih Kabupaten Lokasi anda berada untuk merubah waktu jadwal sholat sesuai lokasi : 
            </p>
              <v-select :options="lokasi" label="lokasi" v-model="lokasinya" :reduce="e => e.id"></v-select>
              <p class="p-2 ">Lokasi Dipilih <span class="font-bold">{{lokasi.filter(e=>e.id==lokasinya)[0].lokasi}}</span></p>
            <hr class="mt-3 mb-3"> -->
            <div class="text-center">
              <button type="button"  class="btn btn-sm btn-dark mr-4 " @click="$router.push('/')"><span class="typcn typcn-arrow-left"></span> Back</button>
              <button type="button"  class="btn btn-sm btn-danger " @click="logout"><span class="typcn typcn-key"></span> Logout</button>
            </div>
              <hr>
          </div>
          <div v-else>
            <div style="height:100px;" class="d-flex justify-content-center align-items-center ">
                <div style="width:100%">
                  <p class="text-center animated bounce infinite text-lg font-semibold text-white">
                    Loading ...
                  </p>
                </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  layout:'app_islamic',
 
  watch:{
    lokasinya(){
      this.$store.state.lokasinya = this.lokasinya;
      localStorage.setItem('lokasinya',this.lokasinya)
    }
  },  
  methods: {
    logout(){
      this.$store.dispatch('logout')
    },
    getLokasi(){
     
        axios.get('https://api.myquran.com/v1/sholat/kota/semua').then(res=>{
            this.lokasi = res.data
            this.$forceUpdate();
        })
    },
  },
  mounted() {
    // this.getLokasi()
    let that=this;
    if(localStorage.getItem('lokasinya')){
            this.lokasinya = localStorage.getItem('lokasinya');
        }else{
            this.lokasinya=this.$store.state.lokasinya
        }
        this.$forceUpdate();
      navigator.geolocation.getCurrentPosition(successFunction, ()=>{});
      function successFunction(position) {
          var lat = position.coords.latitude;
          var lng = position.coords.longitude;
          console.log(lat, lng)
          axios.get(`https://api.bigdatacloud.net/data/reverse-geocode-client?latitude=${lat}&longitude=${lng}&localityLanguage=en`).then(res=>{
            let cekLokasi=that.lokasi.filter(e=>e.lokasi.toLowerCase().indexOf(res.data.locality.toLowerCase())!=-1)
            that.lokasinya=cekLokasi.length>0?cekLokasi[0].id:'2115'
          });
      }
  },
   data(){
    return{
      hello:'helllo',
       lokasinya:"2115",
       lokasi:this.$store.state.lokasi,
    }
  },
}
</script>

